
import { defineComponent } from 'vue';

type Props = {
  class: string;
  disabled: boolean;
  isLoading: boolean;
};

export default defineComponent({
  props: {
    disabled: {
      type: Boolean,
      required: false,
      default: false,
    },
    class: {
      type: String,
      required: false,
      default: '',
    },
    href: {
      type: String,
      required: false,
      default: '',
    },
    hoverStyle: {
      type: String,
      required: false,
      default: '',
    },
  },
  emits: ['click'],
  setup(props, { emit }) {
    const clickLink = (e: any) => {
      if (props.disabled === true) {
        e.preventDefault();
        return;
      }
      // 通常の動作をキャンセルしてemitでイベントを送信
      if ((e.button !== undefined && e.button !== 0) || e.metaKey || e.altKey || e.ctrlKey || e.shiftKey) return;
      e.preventDefault();
      emit('click');
    };

    return {
      props,
      clickLink,
    };
  },
});
