n    \"hover-background-color\": #ffe46c,\r\n    \"hover-color\": #9f0f0f,\r\n    \"focus-background-color\": #ebc312,\r\n    \"focus-color\": #9f0f0f,\r\n    \"disabled-background-color\": #6b80a0,\r\n    \"disabled-color\": #cccccc,\r\n    \"active-background-color\": #ffd517,\r\n    \"active-color\": #9f0f0f,\r\n);\r\n\r\n$danger: (\"-\": #000,\r\n    \"outline-border-color\": #ffffff,\r\n    \"outline-border-color-dark\": #447aca,\r\n    \"default-background-color\": $color-danger ,\r\n    \"default-color\": #ffffff,\r\n    \"hover-background-color\": #f16060,\r\n    \"hover-color\": #ffffff,\r\n    \"focus-background-color\": #ce1919,\r\n    \"focus-color\": #ffffff,\r\n    \"disabled-background-color\": #6b80a0,\r\n    \"disabled-color\": #cccccc,\r\n    \"active-background-color\": #fd0e0e,\r\n    \"active-color\": #ffffff,\r\n);\r\n\r\n\r\n$paper: (\"-\": #000,\r\n    \"outline-border-color\": #dde1e6,\r\n    \"outline-border-color-dark\": #447aca,\r\n    \"default-background-color\": #F0EBE6,\r\n    \"default-color\": #4d4d4d,\r\n    \"hover-background-color\": #d7dbe0,\r\n    \"hover-color\": #4d4d4d,\r\n    \"focus-background-color\": #d7dbe0,\r\n    \"focus-color\": #4d4d4d,\r\n    \"disabled-background-color\": #d7dbe0,\r\n    \"disabled-color\": #4d4d4d,\r\n    \"active-background-color\": #d7dbe0,\r\n    \"active-color\": #4d4d4d,\r\n);\r\n\r\n\r\n$label: (\"-\": #000,\r\n    \"outline-border-color\": #dde1e6,\r\n    \"outline-border-color-dark\": #447aca,\r\n    \"default-background-color\": #717f93,\r\n    \"default-color\": #ffffff,\r\n    \"hover-background-color\": #d7dbe0,\r\n    \"hover-color\": #4d4d4d,\r\n    \"focus-background-color\": #d7dbe0,\r\n    \"focus-color\": #4d4d4d,\r\n    \"disabled-background-color\": #d7dbe0,\r\n    \"disabled-color\": #4d4d4d,\r\n    \"active-background-color\": #d7dbe0,\r\n    \"active-color\": #4d4d4d,\r\n);\r\n\r\n\r\n\r\n\r\n$disabled: (\"-\": #000,\r\n    \"outline-border-color\": #dde1e6,\r\n    \"outline-border-color-dark\": #447aca,\r\n    \"default-background-color\": #bacee9,\r\n    \"default-color\": #2b2b2e,\r\n    \"hover-background-color\": #d7dbe0,\r\n    \"hover-color\": #4d4d4d,\r\n    \"focus-background-color\": #d7dbe0,\r\n    \"focus-color\": #4d4d4d,\r\n    \"disabled-background-color\": #d7dbe0,\r\n    \"disabled-color\": #4d4d4d,\r\n    \"active-background-color\": #d7dbe0,\r\n    \"active-color\": #4d4d4d,\r\n);\r\n\r\n\r\n\r\n\r\n\r\n\r\n$themes-color: (\"theme0\": $theme0,\r\n    \"theme1\": $theme1,\r\n    \"theme2\": $theme2,\r\n    \"success\": $success,\r\n    \"download\": $download,\r\n    \"info\": $info,\r\n    \"action\": $action,\r\n    \"warning\": $warning,\r\n    \"danger\": $danger,\r\n    \"paper\": $paper,\r\n    \"link\": $link,\r\n    \"label\": $label,\r\n    \"caution\": $caution,\r\n    \"disabled\": $disabled,\r\n);\r\n\r\n\r\n\r\n\r\n\r\n\r\n$originalCard: (\"-\": #000,\r\n    \"hedader-background-color\": map-get($theme1, 'default-background-color'),\r\n    \"hedader-color\": map-get($theme1, 'default-color'),\r\n\r\n    \"body-background-color\": map-get($paper, 'default-background-color'),\r\n    \"body-color\": map-get($paper, 'default-color'),\r\n\r\n    \"footer-background-color\": map-get($theme2, 'default-background-color'),\r\n    \"footer-color\": map-get($theme2, 'default-color'),\r\n\r\n    \"outline-border-1px\":1px solid #ffffff,\r\n    \"outline-border-2px\":2px solid #ffffff,\r\n    \"card-white-border\":2px solid #ffffff,\r\n    \"card-white-border-none\":2px solid transparent,\r\n\r\n    \"card-border-color\": #ffffff,\r\n    \"card-border-width\": 2px,\r\n    \"border-radius\": 4px,\r\n    \"card-border-row\":2px solid #ffffff,\r\n);","\r\n@each $name,\r\n$theme in $themes-color {\r\n\r\n\r\n    .bg-#{$name} {\r\n        background-color: map-get($theme, 'default-background-color') !important;\r\n        color           : map-get($theme, 'default-color') !important;\r\n    }\r\n\r\n    .text-#{$name} {\r\n        color: darken(map-get($theme, 'default-background-color'), 5%) !important;\r\n    }\r\n\r\n    .border-#{$name} {\r\n        border-color: darken(map-get($theme, 'default-background-color'), 5%) !important;\r\n    }\r\n\r\n\r\n\r\n    .btn-#{$name} {\r\n        box-shadow: $box-shadow;\r\n        transition: box-shadow 200ms;\r\n\r\n        &:hover {\r\n            box-shadow: $box-shadow-hover;\r\n        }\r\n\r\n        &:not(:disabled):active {\r\n            box-shadow: $box-shadow-activate;\r\n        }\r\n    }\r\n\r\n    .btn-#{$name}-dark,\r\n    .btn-#{$name}-white,\r\n    .btn-#{$name} {\r\n        border          : solid 1px $border-color-dark;\r\n        background-color: map-get($theme, 'default-background-color') !important;\r\n        color           : map-get($theme, 'default-color') !important;\r\n\r\n        &:hover {\r\n            background-color: map-get($theme, 'hover-background-color') !important;\r\n            color           : map-get($theme, 'hover-color') !important;\r\n\r\n        }\r\n\r\n        &:focus {\r\n            background-color: map-get($theme, 'hover-background-color') !important;\r\n            color           : map-get($theme, 'hover-color') !important;\r\n        }\r\n\r\n        &:active {\r\n            background-color: map-get($theme, 'active-background-color') !important;\r\n            color           : map-get($theme, 'active-color') !important;\r\n        }\r\n\r\n        &:disabled,\r\n        &.disabled {\r\n            background-color: map-get($theme, 'disabled-background-color') !important;\r\n            color           : map-get($theme, 'disabled-color') !important;\r\n\r\n            &:focus {\r\n                background-color: map-get($theme, 'disabled-background-color') !important;\r\n                color           : map-get($theme, 'disabled-color') !important;\r\n            }\r\n\r\n            :active {\r\n                background-color: map-get($theme, 'disabled-background-color') !important;\r\n                color           : map-get($theme, 'disabled-color') !important;\r\n            }\r\n        }\r\n\r\n\r\n    }\r\n\r\n    .btn-#{$name}-dark {\r\n        border-color: $border-color-dark;\r\n    }\r\n\r\n    .btn-#{$name}-white {\r\n        border-color: $border-color-white;\r\n    }\r\n}","//--------------------------\r\n//使用する変数\r\n$color-warn  : #eb0000;\r\n$color-action: #e26f2c;\r\n$color-diff  : #e65d0e;\r\n\r\n\r\n\r\n$control-radius:$control-radius;\r\n\r\n$color-back-default : #ffffff;\r\n$color-back-disabled: #f3f3f3;\r\n\r\n$color-fore-default : #242627;\r\n$color-fore-disabled: #2a2a2a;\r\n$color-fore-disabled: #242627;\r\n\r\n$color-line-default  : #122c83;\r\n$color-line-activate : #122c83;\r\n$color-line-disabled : #b8b8b8;\r\n$color-line-warn     :#eb0000;\r\n$color-line-diff     : #e65d0e;\r\n\r\n$dropshadow-hover    : drop-shadow(0px 0px 5px 3px #035afd57);\r\n$dropshadow-diff     : drop-shadow(0px 0px 3px #ff5f0099);\r\n$dropshadow-activate : drop-shadow(0px 0px 3px #065dffa6);\r\n$dropshadow-warn     : drop-shadow(0px 0px 3px #f1000067);\r\n$dropshadow-disabled : drop-shadow(0px 0px 3px #cfcecebb);\r\n\r\n\r\n//------------------------------\r\n//FormControlの配下すべて\r\n.FormControl * {\r\n    position  : relative;\r\n    padding   : 0px;\r\n    margin    : 0px;\r\n    box-sizing: border-box;\r\n\r\n    & :not(i) {\r\n        font       : inherit;\r\n        font-weight: inherit;\r\n        font-family: inherit;\r\n        font-size  : 1em;\r\n    }\r\n}\r\n\r\n//Formコントロール基本\r\n.FormControl {\r\n    //基礎\r\n    padding        : 0px;\r\n    min-height     : 42px;\r\n    height         : auto;\r\n    border-radius  : 6px;\r\n    background-clip: padding-box;\r\n\r\n    font-size  : 16px;\r\n    font-weight: normal;\r\n    max-width  : 100%;\r\n\r\n}\r\n\r\n.FormControl.FormColtrolSlim {\r\n    min-height: 30px;\r\n}\r\n\r\n//Flex\r\n.FormControl {\r\n    display        : flex;\r\n    flex-direction : row;\r\n    flex-wrap      : nowrap;\r\n    justify-content: space-between;\r\n\r\n    .FormControlBody {\r\n        flex: 1 1 auto;\r\n    }\r\n\r\n    .FormControlIcon {\r\n        flex: 0 0 auto;\r\n    }\r\n}\r\n\r\n\r\n\r\n//-----------------------------------------------------------\r\n//枠線、影\r\n.FormControl {\r\n    border-width    : 1px;\r\n    border-style    : solid;\r\n    border-color    : $color-line-default;\r\n    background-color: $color-back-default;\r\n\r\n    transition: border-color 150ms ease-in-out,\r\n        filter 150ms ease-in-out;\r\n    will-change: filter;\r\n\r\n    &.hasWarn:not(.diff) {\r\n        border-color  : $color-line-warn;\r\n        -webkit-filter: $dropshadow-warn;\r\n        -moz-filter   : $dropshadow-warn;\r\n        -ms-filter    : $dropshadow-warn;\r\n        filter        : $dropshadow-warn;\r\n        transform     : translateZ(0);\r\n    }\r\n\r\n    &.diff {\r\n        border-color  : $color-line-default;\r\n        border-color  : $color-line-diff;\r\n        -webkit-filter: $dropshadow-diff;\r\n        -moz-filter   : $dropshadow-diff;\r\n        -ms-filter    : $dropshadow-diff;\r\n        filter        : $dropshadow-diff;\r\n        transform     : translateZ(0);\r\n    }\r\n\r\n    &.disabled {\r\n        border-color    : $color-line-default;\r\n        background-color: $color-back-disabled;\r\n        -webkit-filter  : none;\r\n        -moz-filter     : none;\r\n        -ms-filter      : none;\r\n        filter          : none;\r\n        transform       : translateZ(0);\r\n\r\n        &.diff {\r\n            border-color  : $color-line-default;\r\n            border-color  : $color-line-diff;\r\n            -webkit-filter: $dropshadow-diff;\r\n            -moz-filter   : $dropshadow-diff;\r\n            -ms-filter    : $dropshadow-diff;\r\n            filter        : $dropshadow-diff;\r\n            transform     : translateZ(0);\r\n        }\r\n\r\n    }\r\n\r\n    &.activate {\r\n        border-color  : $color-line-activate;\r\n        -webkit-filter: $dropshadow-activate;\r\n        -moz-filter   : $dropshadow-activate;\r\n        -ms-filter    : $dropshadow-activate;\r\n        filter        : $dropshadow-activate;\r\n        transform     : translateZ(0);\r\n    }\r\n}\r\n\r\n\r\n\r\n//-----------------------------------------------------------\r\n\r\n\r\n\r\n\r\n//-----------------------------------------------------------\r\n\r\n\r\n//Formのインプットとプレースホルダーを格納する要素\r\n.FormControl .FormControlBody {\r\n    display        : flex;\r\n    flex-direction : column;\r\n    flex-wrap      : nowrap;\r\n    justify-content: flex-start;\r\n    align-items    : stretch;\r\n\r\n    font-size  : 1em;\r\n    font-weight: normal;\r\n    width      : 100%;\r\n\r\n    .FormControlLabel {\r\n        flex           : 0 0 auto;\r\n        padding        : 0 3px;\r\n        user-select    : none;\r\n        font-size      : 1rem;\r\n        line-height    : 1rem;\r\n        font-weight    : normal;\r\n        display        : flex;\r\n        flex-wrap      : wrap;\r\n        justify-content: flex-start;\r\n        align-items    : center;\r\n        overflow       : hidden;\r\n\r\n        //ラベル内Span\r\n        [class*=\"FormControlLabel--\"] {\r\n            margin       : 1px 1px 0 1px;\r\n            padding      : 1px 3px;\r\n            border-radius: 3px;\r\n            font-weight  : normal;\r\n            border-width : 0px;\r\n            line-height  : 1em;\r\n            font-size    : 0.7em;\r\n            font-weight  : normal;\r\n            height       : auto;\r\n            flex         : 0 0 auto;\r\n        }\r\n\r\n        .FormControlLabel--Placeholder {\r\n            padding  : 1px 0px 0 0;\r\n            font-size: 0.8em;\r\n            flex     : 1 1 auto;\r\n        }\r\n    }\r\n\r\n    .FormControlInput {\r\n        padding    : 0 0.3em;\r\n        flex       : 1 1 auto;\r\n        width      : 100%;\r\n        height     : auto;\r\n        display    : flex;\r\n        align-items: center;\r\n        user-select: none;\r\n\r\n        select,\r\n        textarea,\r\n        input:not([type=\"checkbox\"].form-check-input) {\r\n            margin-top        : 1px;\r\n            width             : 100%;\r\n            height            : auto;\r\n            background-color  : transparent;\r\n            //標準表示の初期化\r\n            -webkit-appearance: none;\r\n            -moz-appearance   : none;\r\n            appearance        : none;\r\n            border            : 0px;\r\n            outline           : none;\r\n        }\r\n    }\r\n\r\n}\r\n\r\n//------------------------\r\n//アイコン\r\n.FormControl .FormControlIcon {\r\n    width          : 40px;\r\n    display        : flex;\r\n    justify-content: center;\r\n    align-items    : center;\r\n    cursor         : pointer;\r\n    font-size      : 1rem;\r\n\r\n    //------------------------\r\n    //境界線    \r\n    &::before {\r\n        content         : '';\r\n        display         : block;\r\n        position        : absolute;\r\n        display         : none;\r\n        height          : calc(100% - 4px);\r\n        top             : 2px;\r\n        width           : 1px;\r\n        background-color: gray;\r\n    }\r\n\r\n    &[data-sep=\"left\"]::before {\r\n        display: block;\r\n        left   : 0px;\r\n    }\r\n\r\n    &[data-sep=\"right\"]::before {\r\n        display: block;\r\n        right  : 0px;\r\n    }\r\n\r\n    //------------------------\r\n    //アイコン  \r\n\r\n    &::after {\r\n        display    : block;\r\n        position   : relative;\r\n        font-family: \"Font Awesome 5 Free\";\r\n        font-weight: 900;\r\n        overflow   : visible;\r\n    }\r\n\r\n\r\n    &[data-icon=\"warn\"]::after {\r\n        content: '\\f071';\r\n        color  : $color-danger;\r\n    }\r\n\r\n    &[data-icon=\"delete\"]::after {\r\n        content: '\\f55a';\r\n        color  : $color-danger;\r\n    }\r\n\r\n\r\n    &[data-icon=\"calendar\"]::after {\r\n        content: '\\f073';\r\n        color  : $color-action;\r\n    }\r\n}\r\n\r\n//----------------------\r\n//特殊\r\n\r\n.FormControl {\r\n\r\n    .FormControlLabel--Icon-lenMessage {\r\n        color     : #ffffff;\r\n        transition: background-color 200ms;\r\n        opacity   : 1;\r\n\r\n        &[data-maxLen='0'] {\r\n            display: none;\r\n        }\r\n\r\n        &[data-last='0'] {\r\n            background-color: #ff0000;\r\n        }\r\n\r\n        &[data-last='1'] {\r\n            background-color: #fa541d;\r\n        }\r\n\r\n        &[data-last='2'] {\r\n            background-color: #fda00d;\r\n        }\r\n\r\n        &[data-last='3'] {\r\n            background-color: #fda00d;\r\n        }\r\n\r\n        background-color: #26900c;\r\n    }\r\n\r\n    .FormControlLabel--Icon-RowMessage {\r\n        color     : #ffffff;\r\n        transition: background-color 200ms;\r\n        opacity   : 1;\r\n\r\n        &[data-maxLen='0'] {\r\n            display: none;\r\n        }\r\n\r\n        &[data-last='0'] {\r\n            background-color: #ff0000;\r\n        }\r\n\r\n        &[data-last='1'] {\r\n            background-color: #fa541d;\r\n        }\r\n\r\n        background-color: #26900c;\r\n    }\r\n}\r\n\r\n\r\n\r\n//-----------------------------------------------------------\r\n//enable <> disabled\r\n.FormControl {\r\n    color           : $color-fore-default;\r\n    background-color: white;\r\n    color           : $color-fore-default;\r\n\r\n    .FormControlLabel--Placeholder {\r\n        color: #906a51;\r\n    }\r\n\r\n    .FormControlLabel--Icon-Require {\r\n        color           : #ffffff;\r\n        background-color: #af0000;\r\n    }\r\n\r\n    &.disabled {\r\n        color: $color-fore-disabled !important;\r\n\r\n        [class*=\"FormControlLabel--Icon\"] {\r\n            color           : #ffffff !important;\r\n            background-color: #727272 !important;\r\n        }\r\n\r\n        select,\r\n        input {\r\n            -webkit-text-fill-color: inherit;\r\n            color                  : inherit;\r\n            opacity                : 1 !important;\r\n        }\r\n\r\n        .FormControlIcon:not(.FormControlIcon[data-icon=\"warn\"]) {\r\n            cursor: default;\r\n\r\n            &::after {\r\n                color: inherit;\r\n                color: #908e8e !important;\r\n            }\r\n        }\r\n\r\n        .FormListSelect>.FormListSelectItem {\r\n            cursor: default;\r\n        }\r\n\r\n    }\r\n}\r\n\r\n//------------------------------------------\r\n\r\n\r\n//インプットパーツの集合体、内部パーツ同士が隣接した部分は角を丸くしない\r\n.FormGroup {\r\n    position       : relative;\r\n    width          : 100%;\r\n    max-width      : 100%;\r\n    display        : flex;\r\n    justify-content: space-between;\r\n    align-items    : stretch;\r\n    border-radius  : $control-radius;\r\n\r\n    >*:not(.FormGroupIcon) {\r\n        flex-grow  : 1;\r\n        flex-shrink: 1;\r\n        width      : auto;\r\n        flex-basis : auto;\r\n    }\r\n\r\n    >.FormGroup-Item50WithIcon {\r\n        width: calc(50% - 1em);\r\n    }\r\n\r\n    >.FormGroupIcon {\r\n        height          : auto;\r\n        flex-grow       : 1;\r\n        min-width       : 2em;\r\n        padding         : 0 0.5em;\r\n        min-height      : 0px;\r\n        display         : flex;\r\n        justify-content : center;\r\n        align-items     : center;\r\n        background-color: rgb(231, 231, 231);\r\n        border          : solid 1px $color-line-default;\r\n    }\r\n\r\n    >* {\r\n        border-top-left-radius    : 0px;\r\n        border-bottom-left-radius : 0px;\r\n        border-top-right-radius   : 0px;\r\n        border-bottom-right-radius: 0px;\r\n        border-top-width          : 1px;\r\n        border-right-width        : 0px;\r\n        border-bottom-width       : 1px;\r\n        border-left-width         : 1px;\r\n    }\r\n\r\n\r\n    >*:first-child {\r\n        border-top-left-radius   : $control-radius;\r\n        border-bottom-left-radius: $control-radius;\r\n    }\r\n\r\n    >*:last-child {\r\n        border-top-right-radius   : $control-radius;\r\n        border-bottom-right-radius: $control-radius;\r\n        border-right-width        : 1px;\r\n    }\r\n\r\n    >.FormGroupIcon {\r\n        padding   : 0.2em 0em;\r\n        min-height: 24px;\r\n\r\n        >* {\r\n            transform: rotate(90deg);\r\n        }\r\n    }\r\n\r\n\r\n\r\n    @each $breakpoint in map-keys($grid-breakpoints) {\r\n        $infix: breakpoint-infix($breakpoint, $grid-breakpoints);\r\n\r\n        @include media-breakpoint-up($breakpoint) {\r\n            >.FormGroup#{$infix}-Item50WithIcon {\r\n                width: calc(50% - 1em) !important;\r\n            }\r\n\r\n            &.FormGroup#{$infix}-Column {\r\n                flex-direction: column;\r\n\r\n                >*:first-child {\r\n                    border-top-left-radius    : $control-radius;\r\n                    border-top-right-radius   : $control-radius;\r\n                    border-bottom-left-radius : 0px;\r\n                    border-bottom-right-radius: 0px;\r\n                }\r\n\r\n                >* {\r\n                    border-top-left-radius    : 0px;\r\n                    border-bottom-left-radius : 0px;\r\n                    border-top-right-radius   : 0px;\r\n                    border-bottom-right-radius: 0px;\r\n\r\n                    border-top-width   : 1px;\r\n                    border-right-width : 1px;\r\n                    border-bottom-width: 0px;\r\n                    border-left-width  : 1px;\r\n                }\r\n\r\n                >*:last-child {\r\n\r\n                    border-top-left-radius    : 0px;\r\n                    border-top-right-radius   : 0px;\r\n                    border-bottom-left-radius : $control-radius;\r\n                    border-bottom-right-radius: $control-radius;\r\n                    border-bottom-width       : 1px;\r\n                }\r\n\r\n             