// [ Com ]-------------------------------------------------------------------------
import { InjectionKey } from 'vue';
import { createStore, Store } from 'vuex';
import { Pdf } from '@/script/com/com-pdf';
import { Font } from '@pdfme/ui';

// --------------------------------------------------------------------------------
export namespace StoreApp {
  // --------------------------------------------------------------------------------
  namespace Mutations {
    export namespace App {
      export const Init = 'App-SetInit';
    }
    export const SetPdfFont = 'SetPdfFont';
  }
  namespace Actions {
    export namespace App {
      export const Init = 'App-Init';
    }
  }

  interface State {
    app: {
      isInit: boolean;
    };
    pdf: {
      font: Font
    }
  }
  const state: State = {
    app: {
      isInit: false,
    },
    pdf: {
      font: {
      }
    }
  };

  // --------------------------------------------------------------------------------
  export const StoreKey: InjectionKey<Store<State>> = Symbol();
  export const Store = createStore<State>({
    state: state,
    mutations: {
      // [ App ]-----------------------------------
      [Mutations.App.Init]: (state) => {
        state.app.isInit = true;
      },
      [Mutations.SetPdfFont]: (state, payload) => {
        state.pdf.font = payload;
        // console.log('SetPdfFont', payload)
      },
    },
    // --------------------------------------------------------------------------------
    actions: {
      [Actions.App.Init]: (store) => {
        // temp
      },
    }, //-actions-
  });
  // --------------------------------------------------------------------------------
  export namespace Methods {
    export namespace App {
      export const Init = async () => {
        try {
          Store.commit(Mutations.App.Init);
          Store.commit(Mutations.SetPdfFont, await Pdf.GetFont());
          return true;
        } catch (error) {
          console.error(error);
          return false;
        }
      };
    }
  }
}
