
import { defineComponent, reactive, ref, watch, onMounted, nextTick } from 'vue';
export default defineComponent({
  props: {
    isShow: {
      type: Boolean,
      required: true,
    },
  },
  setup(props) {
    const elmAccordion = ref<HTMLElement>();
    const state = reactive({
      height: 'auto',
    });
    watch(
      () => props.isShow,
      () => {
        resize();
      }
    );
    if (props.isShow == true) {
      state.height = 'auto';
    } else {
      state.height = '0px';
    }
    const resize = async () => {
      try {
        if (elmAccordion.value) {
          //要素のサイズ確認
          const height = elmAccordion.value.getBoundingClientRect().height;
          if (props.isShow == true) {
            state.height = height + 'px';
            setTimeout(() => {
              state.height = 'auto';
            }, 300);
          } else {
            state.height = String(elmAccordion.value.clientHeight ) + 'px';
            await nextTick();
            setTimeout(() => {
              state.height = '0px';
            }, 100);
          }
        }
      } catch {
        state.height = '0px';
      }
    };
    onMounted(() => {
      resize();
    });
    return {
      state,
      props,
      elmAccordion,
    };
  },
});
