
type Props = {
  //Form系コントロール統一要素
  placeholder: string;
  class: string;
  inputClass: string;
  require: boolean;
  disabled: boolean;
};

import { defineComponent, reactive, ref, watch, onMounted } from 'vue';
export default defineComponent({
  props: {
    placeholder: {
      type: String,
      required: false,
      default: '',
    },
    class: {
      type: String,
      required: false,
      default: '',
    },
    inputClass: {
      type: String,
      required: false,
      default: '',
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false,
    },
    require: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  setup(props: Props) {
    return {
      props,
    };
  },
});
