lay: flex;\r\n      flex-direction: row;\r\n      flex-wrap: nowrap;\r\n      justify-content: space-between;\r\n      align-items: stretch;\r\n\r\n      .toastItem--col1 {\r\n        position: relative;\r\n        width: 40px;\r\n        flex-grow: 0;\r\n        justify-content: center;\r\n        font-size: 22px;\r\n        align-items: center;\r\n        padding: 0 0 0 10px;\r\n\r\n        > .toastItem-icon::after {\r\n          font-family: 'Font Awesome 5 Free';\r\n          font-weight: 900;\r\n        }\r\n      }\r\n\r\n      .toastItem--col2 {\r\n        position: relative;\r\n        width: 50px;\r\n        flex-grow: 1;\r\n        padding: 10px 5px 10px 10px;\r\n\r\n        .toastItem--title {\r\n          position: relative;\r\n          font-size: 18px;\r\n          // font-weight: bold;\r\n          white-space: pre-line;\r\n        }\r\n\r\n        .toastItem--message {\r\n          position: relative;\r\n          font-size: 16px;\r\n          white-space: pre-line;\r\n        }\r\n      }\r\n\r\n      .toastItem--col3 {\r\n        position: relative;\r\n        width: 50px;\r\n        flex-grow: 0;\r\n        display: flex;\r\n        align-items: flex-start;\r\n        justify-content: flex-end;\r\n        padding: 5px 10px 0 0;\r\n        cursor: pointer;\r\n        font-size: 22px;\r\n      }\r\n    }\r\n\r\n    > .toastItem--barContainer {\r\n      padding: 3px 6px 3px 6px;\r\n\r\n      > .toastItem--bar {\r\n        position: relative;\r\n        height: 6px;\r\n        width: 0px;\r\n        transition: width 50ms;\r\n        will-change: width;\r\n        border-radius: 3px;\r\n      }\r\n    }\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
